@import 'src/styles/base';
.branch_item_container {
  overflow: hidden;
  position: relative;

  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-color: transparent;
  border-radius: 10px;
  .thumbnail {
    height: 187px;
    width: 100%;
    object-fit: cover;
  }

  .branch_name {
    margin-left: 10px;
    display: -webkit-box;
    -webkit-line-clamp: 2; /* number of lines to show */
    -webkit-box-orient: vertical;
    font-size: 18px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 700;
  }
  .branch_address {
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2; /* number of lines to show */
    -webkit-box-orient: vertical;
    font-size: 18px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 300;
    margin-right: 20px;
  }

  .image_merchant {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

    width: 48px;
    height: 48px;
    border-radius: 24px;
  }

  .city_container {
    background-color: #c4a732;
    position: absolute;
    top: 170px;
    width: 150px;
    display: flex;
    align-items: center;
    border-radius: 0 8px 8px 0;
    height: 35px;
    left: 0;
  }

  .city_text {
    margin-left: 10px;
    font-size: 20px;
    color: white;
    font-family: 'Google Sans', sans-serif;
    font-weight: 600;
  }

  .branch_phone {
    font-size: 18px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 300;
  }
}

