.mh_home_page_about {
  .mh_home_page_content {
    display: flex;
    flex-direction: column;
    align-items: center;
    .background_home {
      position: absolute;
      top: 0;
      bottom: auto;
      left: 0;
      right: 0;
      z-index: -1;
    }
    .mh_home_page_about {
      margin-top: 46px;
      .about_title {
        font-size: 2.5rem;
        font-weight: 900;
        font-family: 'Google Sans', sans-serif;
      }
      .about_content {
        margin-top: 18px;
        font-size: 1.7rem;
        font-weight: 400;
        font-family: 'Google Sans', sans-serif;
      }
    }

    .banner_home_urban {
      margin-top: 36px;
      width: 100%;
      height: 500px;
    }
  }

  .mh_home_layout {
    width: 85%;
  }

  .banner_home_about1 {
    margin-top: 18px;
    width: 100%;
    height: 2;
  }
  .mh_home_page_daohua {
    width: 85%;
    margin-top: 36px;
    .title {
      font-size: 2.5rem;
      font-weight: 900;
      font-family: 'Google Sans', sans-serif;
    }
  }

  @media only screen and (max-width: 1280px) {
    .banner_home_about1 {
      height: 33vw;
    }
  }
  .banner_home_daohua {
    margin-top: 36px;
    width: 100%;
    height: 500px;
  }

  .membership_about {
    width: 100%;
    height: 500px;
  }

  .membership_about1 {
    align-self: center;
    width: 100%;
    height: 23vw;
  }

  .membership_recruitment {
    width: 100%;
    height: 500px;
  }

  .membership_recruitment1 {
    align-self: center;
    width: 100%;
    height: 23vw;
  }
  .membership_container {
    width: 85%;
    align-self: center;
    margin: 0 auto;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .membership_recruitment_container {
    align-self: center;
    height: 100%;
    width: 85%;
    position: absolute;
    margin: 0 auto;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .membership_title {
    align-self: center;
    font-size: 2.5rem;
    font-weight: 900;
    font-family: 'Google Sans', sans-serif;
  }
  .membership_content {
    margin-top: 14px;
    font-size: 1rem;
    font-weight: 400;
    font-family: 'Google Sans', sans-serif;
  }

  .membership_btn_section {
    margin-top: 20px;
    .membership_btn {
      background-color: transparent;
      flex-direction: row;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      .membership_btn_text {
        color: #b99c31;
        font-family: 'Google Sans', sans-serif;
        font-size: 1.2rem;
        margin-right: 4px;
      }
    }
  }
  .nextArrowOtherSlide {
    position: absolute;
    right: 100px;
    top: 150px;
    width: 40px;
    height: 40px;
    border-radius: 20px;

    background: #1f2d50;
    opacity: 0.5;
  }

  .preArrowOtherSlide img,
  .nextArrowOtherSlide img {
    object-fit: contain;
    width: 40px;
    height: 40px;
    padding: 10px;
  }
  .preArrowOtherSlide {
    position: absolute;
    left: 50px;
    top: 150px;
    width: 40px;
    height: 40px;
    border-radius: 20px;

    background: #1f2d50;
    opacity: 0.5;
  }

  .nextArrowOtherSlide::before,
  .preArrowOtherSlide::before {
    display: none;
  }
  .slick-prev {
    // left: 3% !important;
    z-index: 1;
  }
  .slick-next {
    right: 3% !important;
    z-index: 1;
  }

  @media screen and (max-width: 600px) {
    .banner_home_daohua {
      margin-top: 36px;
      width: 100%;
      height: 180px;
    }
    .membership_about1 {
      align-self: center;
      width: 100%;
      height: 200px;
    }

    .membership_about {
      width: 100%;
      height: 500px;
    }

    .membership_recruitment {
      height: 700px;
    }

    .banner_home_about1 {
      height: 180px;
    }

    .mh_home_page_daohua {
      width: 95%;
    }

    .membership_container {
      width: 95%;
    }

    .membership_recruitment_container {
      width: 95%;
    }

    .membership_recruitment1 {
      height: 200px;
    }

    .mh_home_layout {
      width: 95%;
    }
  }
}
